import {
    get,
    post,
    deletes,
    put
} from './axios.js'; //axios

let base_api = process.env.API_HOST ? process.env.API_HOST : '..' //api

//
const getProvince = (data) => {
    return post(`${base_api}common_api/Master/getProvince`,data)
}
//
const getCity = (data) => {
    return post(`${base_api}common_api/Master/getCity`,data)
}
//
const getArea = (data) => {
    return post(`${base_api}common_api/Master/getArea`,data)
}
//  
const sendRegisterSms = (data) => {
    return post(`${base_api}common_api/User/sendRegisterSms`,data)
}
//
const getUserType = (data) => {
    return post(`${base_api}common_api/User/getUserType`,data)
}
//
const regUser = (data) => {
    return post(`${base_api}common_api/User/register`,data)
}
//  
const sendRegisterSmsM = (data) => {
    return post(`${base_api}common_api/Master/sendRegisterSms`,data)
}
//
const masterRegister = (data) => {
    return post(`${base_api}common_api/Master/masterRegister`,data)
}




export default {
    getProvince,
    getCity,
    getArea,
    sendRegisterSms,
    getUserType,
    regUser,
    sendRegisterSmsM,
    masterRegister
}


