export default {
    //  
    checkPhone: function(val) {
        var phoneReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
        if(!phoneReg.test(val)) {
            return false
        }
        return true
    },
    // 
    //  a-z  
    objKeySort: function(obj) {
        var newkey = Object.keys(obj).sort();
    //ObjectkeysArraysortnewkey
        var newObj = {};//
        for (var i = 0; i < newkey.length; i++) {//newkey
            newObj[newkey[i]] = obj[newkey[i]];//
        }
        return newObj;//
    },
    // 
    //  sign 
    changeString: function(json,string) {
        var arr = new Array();
        for(var i in json) {
            arr.push(i);
            arr.push(json[i]);
        }
        var infoString = arr.join('');
        infoString = infoString + string
        return infoString
    },
    //  
    checkPhone(val) {
        var phoneReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
        if (!phoneReg.test(val)) {
            return false;
        }
        return true;
    }
}


