<template>
    <div class="master_two">
        <div class="master_info">
            <div class="invitate_code">
                <span>:</span>
                <input type="text" placeholder="" disabled v-model="master.beInviteCode">
            </div>
            <div class="choose_radio" @click="showLocation">
                <span>:</span>
                <span>{{locationName}}</span>
            </div>
            <div class="ele">
                <input type="text" maxlength="11" v-model.trim="master.phone" placeholder="()">
                <button class="get_code" @click="getCode" v-if="!showTime">{{getCodeText}}</button>
                <button class="get_code" v-else>{{getCodeText}}</button>
            </div>
            <div class="ele">
                <input type="text" maxlength="6" v-model.trim="master.code" placeholder="()">
            </div>
            <div class="ele">
                <input type="password" v-model.trim="master.password" placeholder="()">
                <img class="delete_btn" @click="deletePass" src="../../../static/image/delete.png" alt="">
            </div>
            <div class="ele">
                <input type="password" v-model.trim="repeatPass" placeholder="()">
                <img class="delete_btn" @click="deleteRepass" src="../../../static/image/delete.png" alt="">
            </div>
            <label for="agree" class="master_agree" >
                <input type="checkbox" id="agree" @click="changeChecked(checked)" v-model="checked">
                <router-link to="/masterRegister/masterService"></router-link>
                <router-link to="/masterRegister/noticeBook"></router-link>
            </label>
        </div>
        <p class="complete_btn" v-if="compleTxt == '...'" >{{compleTxt}}</p>
        <p class="complete_btn" v-else @click="completeReg">{{compleTxt}}</p>
        <citySelect :isShowLocation = isShowLocation 
                    v-on:hideLocationHandleA="hideLocationEventA"
                    v-on:hideLocationHandleB="hideLocationEventB"
                    :nullObj = 'nullObj'
        ></citySelect>
        <div class="alert_tips" v-if="hideTips">
            <div class="notice_book">
                <div class="nb_title">
                    <em></em>
                    <span></span>
                    <img @click="close_tips" src="static/image/close_icon.png" alt="">
                </div>
                <div class="text_det">
                    <p class="page_des_one"></p>
                    <p class="page_des_one"></p>
                    <p class="page_des_one"></p>
                    <p class="big_sentence">1 </p>
                    <p class="big_sentence">2</p>
                    <p class="big_sentence">
                    3
                    
                    
                    </p>
                </div>
                <p class="already_understand" @click="registerHandle"></p>
            </div>
        </div>
    </div>
</template>
<script>
import qs from 'qs';
import citySelect from '@/components/citySelect'
import md5 from 'js-md5';
import common from "../../public/common.js";  //
import api from "@/http/api.js";
export default {
    name: 'masterRegisterPage',
    components: {
        citySelect
    },
    data() {
        return {
            isShowLocation: false,                    //             
            locationName: '',       //
            showTime: false,
            time: 60,                                 //
            getCodeText: '',
            provinceText: '',
            compleTxt: '',
            repeatPass: '',                     //
            checked: false,                      //
            master: {
                beInviteCode: '',               //
                phone: '',                      //
                code: '',                       //
                password: '',                   //,
                province: '',                   //
                city: '',                       //
                area: ''                        //
            },
            info: {
                sign:'431104c0-984c-4415-90ed-847938e5cb00'
            },
            nullObj: {},                         //
            hideTips: false,                     //
            isRegistering: false
        }
    },
    created() {
        this.master.beInviteCode = this.$route.query.code;
    },
    methods: {
        hideLocationEventA() {
            this.isShowLocation = false
        },
        hideLocationEventB(a,b) {
            this.nullObj = b;
            this.isShowLocation = a
            this.locationName = ''
            for(var i in b) {
                this.locationName += ' '+b[i];
            }
            this.master.province = b.Province
            this.master.city = b.City
            this.master.area = b.Area
        },
        showLocation() {    //
            this.isShowLocation = true;
            for(var i in this.nullObj) {
                delete(this.nullObj[i])
            }
        }, 
        deletePass() {
            this.master.password = ''
        },
        deleteRepass() {
            this.repeatPass = ''
        },
        getCode() {
            let info = {
                phone: this.master.phone
            },
            _that = this;
            info.sign = md5(common.changeString(common.objKeySort(info),'431104c0-984c-4415-90ed-847938e5cb00')).toUpperCase();

            //
            if(!this.master.phone) {
                this.layer.open({
                    content: '!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return 
            }
            if(!common.checkPhone(this.master.phone)) {
                this.layer.open({
                    content: '!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return
            }
            this.getCodeText = '...'
            this.showTime = true
           //

            api.sendRegisterSmsM(info)
            .then(res => {
                if(res.data.header.rspCode == 0) {
                    let timer = setInterval(() =>{
                        _that.time--;
                        _that.getCodeText = _that.time+'S'
                        if(_that.time < 1) {
                            _that.time = 60
                            _that.showTime = false
                            _that.getCodeText = ''
                            clearInterval(timer)
                        }
                    },1000)
                }
                if(res.data.header.rspCode == 401) {
                    _that.layer.open({
                        content: res.data.header.rspMsg
                        ,skin: 'msg'
                        ,time: 2 //2
                    })
                    _that.getCodeText = ''
                    _that.showTime = false
                }
            }).catch(err => {
                console.log(err);
            })
        },
        // 
        changeChecked(item) {
            if(item) {
                item = false
            }else {
                item = true
            }
        },
        // 
        completeReg() {
            if(!this.master.province) {
                this.layer.open({
                    content: '!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return
            }
            if(!this.master.city) {
                this.layer.open({
                    content: '!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return
            }
            if(!this.master.area) {
                this.layer.open({
                    content: '!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return
            }
            if(!this.master.phone) {
                this.layer.open({
                    content: '!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return
            }
            if(!this.master.code) {
                this.layer.open({
                    content: '!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return
            }
            if(!this.master.password) {
                this.layer.open({
                    content: '!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return
            }
            if(this.master.password.length <6 || this.master.password.length>20) {
                this.layer.open({
                    content: '6~20!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return
            }
            if(!this.repeatPass) {
                this.layer.open({
                    content: '!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return
            }
            if(this.repeatPass.length<6 || this.repeatPass.length > 20) {
                this.layer.open({
                    content: '6~20!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return
            }
            if(this.repeatPass !== this.master.password) {
                this.layer.open({
                    content: '!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return
            }
            if(!this.checked) {
                this.layer.open({
                    content: '!'
                    ,skin: 'msg'
                    ,time: 2 //2
                })
                return
            }

            this.compleTxt = '...';
            this.hideTips = true;
        },
        // 
        close_tips() {
            this.hideTips = false
        },
        //  
        registerHandle() {
            if(this.isRegistering) return
            let _that = this;
            this.master.sign = md5(common.changeString(common.objKeySort(this.master),'431104c0-984c-4415-90ed-847938e5cb00')).toUpperCase();
            this.isRegistering = true;
            api.masterRegister(this.master)
            .then(res => {
                if(res.data.header.rspCode == 0) {
                    _that.$router.push('/masterRegister/masterSuccess')
                }
                if(res.data.header.rspCode == 401) {
                    delete this.master.sign;
                    _that.isRegistering = false;
                    _that.layer.open({
                        content: res.data.header.rspMsg
                        ,skin: 'msg'
                        ,time: 2 //2
                    })
                    _that.compleTxt = '';
                }
            }).catch(err => {
                _that.isRegistering = false;
                delete _that.master.sign;
                console.log(err);
            })
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../public/common.less";
.master_two {
    width: 100%;
    height: 100%;
    background: #F5F5F5;
    .master_info {
        span {
            margin-right: 20px;
            font-size: 26px; /*px*/
        }
        .choose_radio,.invitate_code {
            position: relative;
            .display-flex;
            .justify-content(flex-start);
            .align-items(center);
            height: 81px;
            margin-bottom: 10px;
            padding: 0 20px;
            background: #fff;
            .border(#fff);  /*px*/
            &.active {
                .border(red);   /*px*/
            }
            label {
                width: 120px;
                margin-right: 50px;
                font-size: 26px; /*px*/
            }
            input[type='text'] {
                border: none;
                font-size: 28px; /*px*/
                letter-spacing: 2px;
                background: none;
            }
            input[type="radio"] + label::before {
                content: "\a0"; /**/
                display: inline-block;
                vertical-align: middle;
                font-size: 18px;
                width: 20px;
                height: 20px;
                margin-right: .4em;
                border-radius: 50%;
                border: 1px solid #686464;  /*px*/
                text-indent: .15em;
                line-height: 1; 
            }
            input[type="radio"]:checked + label::before {
                background-color: #686464;  
                background-clip: content-box;
                padding: 5px;
                width: 10px;
                height: 10px;
            }
            input[type="radio"] {
                position: absolute;
                clip: rect(0, 0, 0, 0);
            }
            select {
                height: .8rem;
                padding-left: .15rem;
                padding-right: .5rem;
                box-sizing: border-box;
                border: solid 1px #575556;
                appearance:none;
                -moz-appearance:none;
                -webkit-appearance:none;
                background: url("../../../static/image/xiangxia.png") no-repeat scroll right center transparent;               
                &::-ms-expand { display: none; }
            }

        }
        .ele {
            .display-flex;
            .justify-content(space-between);
            .align-items(center);
            padding: 0 20px 1px;
            .border(#fff);  /*px*/
            background: #fff;
            margin-bottom: 2px; /*px*/
            &:nth-of-type(2) {
                input {
                    background: none;
                }
            }
            &:nth-of-type(4) {
                margin-bottom: 10px;
            }
            input {
                margin: 1px 0;
                height: 80px;
                border: none;
                font-size: 28px; /*px*/
                letter-spacing: 2px;
                background: none;
            }
            .get_code {
                padding: 10px;
                background: none;
                border: 1px solid #000; /*px*/
                .border-radius(8px);
                font-size: 24px; /*px*/
                -webkit-tap-highlight-color: transparent;
            }
            .count_down {
                padding: 10px;
                .border(#d3d3d3);
                .border-radius(8px);
                 
                color: #d3d3d3;
            }
            .delete_btn {
                width: 40px;
                height: 40px;
            }
        }
        .master_agree {
            display: block;
            padding: 40px 20px;
            margin-bottom: 3px;
            .border(#f5f5f5);  /*px*/
            &.active {
                .border(red);   /*px*/
            }
            input[type='checkbox'] {
                width: 24px;
                height: 24px;
                margin-right: 6px;
            }
            a {
                font-size: 24px; /*px*/
                color: #4AAB80;
            }
        }
    }
    .complete_btn {
        width: 670px;
        height: 82px;
        margin: 0 auto 0;
        .border-radius(16px);
        background: #4AAB80;
        color: #fff;
        text-align: center;
        line-height: 82px;
        font-size: 32px; /*px*/
    }
    .alert_tips {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0,.6);
        .notice_book {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            width: 600px;
            height: 760px;
            background: #fff;
            .nb_title {
                .display-flex;
                .justify-content(space-between);
                .align-items(center);
                height: 100px;
                padding: 0 20px;
                border-bottom: 2px solid #dfdfdf;
                span {
                    font-size: 26px;
                    color: #333;
                }
                img {
                    width: 35px;
                }
            }
            .text_det {
                height: 540px;
                padding: 15px 20px;
                overflow-y: auto;
                .rw_title {
                    text-align: center;
                    color: #404040;
                    font-size: 48px;
                }
                .page_des_one {
                    font-size: 28px;
                }
                .big_sentence {
                    text-indent: 2em;
                    letter-spacing: 8px;
                    font-size: 26px;
                }
            }
            .already_understand {
                height: 100px;
                background: #4AAB80;
                text-align: center;
                line-height: 100px;
                font-size: 28px;
                letter-spacing: 4px;
                color: #fff;
            }
        }
    }
}

</style>



